.card {
  width: 451px;
  height: auto;
  border-radius: 0;
  box-shadow: 5px 5px 5px #e2e2e2;
}

.cardContainer {
  display: flex;
}

.cardImage {
}

.cardInfo {
  position: relative;
  width: 371px;
  box-sizing: border-box;
  max-width: 271px;
  padding: 10px 20px;
}

.cardTitle {
  max-width: 200px;
  font-size: 16px;
  font-weight: 500;
  margin: 0 0 7px 0;
  line-height: 120%;
}

.date {
  color: #827e7e;
  margin-top: 7px;
  margin-bottom: 9px;
}

.containerTag {
  padding: 2px 4px;
  align-items: center;
}

.wrapperTags {
  height: 27px;
  overflow: hidden;
}

.cardParagraph {
  position: relative;
  width: 100%;
  font-size: 12px;
  margin: 0;
  margin-top: 10px;
  white-space: wrap;
}

