.containerDescription {
  height: 120px;
  width: 230px;
}

.estimation {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 2px solid #e9d100;
  font-size: 12px;
}

.rate {
  position: absolute;
  font-size: 16px;
  bottom: 10px;
}

.green-border {
  border: 2px solid #66e900;
}

.yelow-border {
  border: 2px solid #e9d100;
}

.orange-border {
  border: 2px solid #e97e00;
}

.red-border {
  border: 2px solid #e90000;
}

@media (max-width: 468px) {
  .card {
    width: 100%;
    height: auto;
  }

  .cardInfo {
    width: 100%;
    min-height: 270px;
    padding: 5px 7px;
  }

  .cardParagraph {
    width: auto;
  }

  .cardImage {
    min-width: 100px;
  }
}
